<template>
  <TheHeader />
  <Board
    :boardArray="boardArray"
    @clicked="handleClick"
    :wonGame="wonGame"
    :mines="currentSize.mines"
    :markedMines="marked"
    @marked="handleMark"
  />
  <TheLevelPicker @changeSize="changeSize" />
  <button @click="restartGame">Restart</button>
</template>

<script>
import Board from "./components/Board.vue";
import TheHeader from "./components/TheHeader.vue";
import TheLevelPicker from "./components/TheLevelPicker";

export default {
  name: "App",
  components: {
    Board,
    TheHeader,
    TheLevelPicker,
  },
  data() {
    return {
      pickedSize: "intermediate",
      currentSize: null,
      boardArray: [], //2d array with square objects „Ç≥„Éû„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÅåÂÖ•„Å£„Å¶„ÅÑ„ÇãÔºíÊ¨°ÂÖÉÈÖçÂàó
      squaresWithMines: [], //Squares with mines Âú∞Èõ∑„ÇíÊåÅ„Å£„Å¶„ÅÑ„Çã„Ç≥„Éû
      nowPlaying: true,
      wonGame: false,
      shownArray: [], //Save revealed squares in here ‰∏≠Ë∫´ÔºàÊï∞Â≠ó„Å®„ÅãÔºâË¶ã„Åõ„Çâ„Çå„Åü„Ç≥„Éû„Çí„Åì„Åì„Åß‰øùÂ≠ò
      marked: 0, //Number of flags placed „Å§„Åë„Çâ„Çå„ÅüÊóó„ÅÆÂÄãÊï∞
    };
  },
  watch: {
    //Check win every time a square is marked. If all mines are marked = win
    //This relies on user not marking every square
    //Êóó„Çí„Å§„Åë„Çã„Åü„Å≥„Å´win„Çí„ÉÅ„Çß„ÉÉ„ÇØ„ÄÇ„Åô„Åπ„Å¶„ÅÆÂú∞Èõ∑„Å´„Éû„Éº„ÇØ„Åå‰ªò„ÅÑ„Å¶„ÅÑ„Çå„Å∞win„Å®„Åó„Åæ„Åô
    //Êóó„ÇíÂÖ®„Ç≥„Éû„Å´„Å§„Åë„Å™„ÅÑ„Åì„Å®„ÇíÂâçÊèê
    marked() {
      let theyWin = this.squaresWithMines.every((square) => square.marked);
      if (theyWin) {
        this.wonGame = true;
        this.nowPlaying = false;
      }
    },
  },
  methods: {
    changeSize(size) {
      this.pickedSize = size;
      this.restartGame();
    },
    restartGame() {
      this.boardArray = [];
      this.squaresWithMines = [];
      this.nowPlaying = true;
      this.wonGame = false;
      this.shownArray = [];
      this.marked = 0;
      this.setUpBoard();
    },
    //left click Â∑¶„ÇØ„É™„ÉÉ„ÇØ
    handleClick(row, column) {
      if (!this.nowPlaying) {
        return;
      }
      let square = this.boardArray[row][column];
      if (square.mine && !square.marked) {
        this.nowPlaying = false;
        this.squaresWithMines.forEach((mine) => (mine.revealed = true));
      }
      if (!square.marked) {
        square.revealed = true;
        if (square.number === 0) {
          this.showSquares(row, column);
        }
      }
    },
    //right clickÔºöadd flag Âè≥„ÇØ„É™„ÉÉ„ÇØ:Êóó„Çí„Å§„Åë„Çã
    handleMark(row, column) {
      if (!this.nowPlaying) {
        return;
      }
      let square = this.boardArray[row][column];
      if (!square.revealed) {
        square.marked = !square.marked;
        square.marked ? this.marked++ : this.marked--;
      }
    },
    //Click reveals some squares „ÇØ„É™„ÉÉ„ÇØ„Åó„Å¶„ÅÇ„Çã„Ç≥„Éû„Åå‰∏≠Ë∫´„ÇíË¶ã„Åõ„Çã„ÇÑ„Å§
    showSquares(row, column) {
      let square = this.boardArray[row][column];
      let containsZero = square.number === 0;
      let notInArray = this.shownArray.every(
        (square) => !(square.row === row && square.column === column)
      );
      let notAMine = !square.mine;
      let notMarked = !square.marked;
      //If zero and not in shownArray Value„Åå„Çº„É≠„ÅßshownArray„Å´‰øùÂ≠ò„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà
      if (containsZero && notInArray && notMarked) {
        square.revealed = true;
        this.shownArray.push(square);
        //Do same thing to neighbors „Çº„É≠„ÅÆÂ†¥Âêà„ÅØÈö£„ÅÆ„Ç≥„Éû„ÇÇÂêåÊßò
        const neighbors = this.findNeighbors(row, column);
        neighbors.forEach((neighbor) =>
          this.showSquares(neighbor.row, neighbor.column)
        );
      }
      //If not zero, not mine, not in shownArray „Çº„É≠„Åß„ÅØ„Å™„ÅÑ„ÅåÂú∞Èõ∑„Åß„ÇÇ„Å™„ÅÑ„ÄÅ„Åæ„Å†shownArray„Å´‰øùÂ≠ò„Åó„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà
      else if (notAMine && notInArray && notMarked) {
        square.revealed = true;
        this.shownArray.push(square);
      } else return;
    },
    //Find neighbors, return in array  „ÅÇ„Çã„Éû„Çπ„ÅÆÈö£„Å´„ÅÇ„Çã„Éû„Çπ„ÇíË¶ã„Å§„Åë„Å¶„ÄÅÈÖçÂàó„Å´„ÅÑ„Çå„Å¶Êàª„Åô
    findNeighbors(row, column) {
      let neighborsArray = [];
      for (let i = row - 1; i <= row + 1; i++) {
        for (let j = column - 1; j <= column + 1; j++) {
          //Prevent out of range errors Out of range  „Ç®„É©„Éº„Å´„Å™„Çâ„Å™„ÅÑ„Çà„ÅÜ„Å´„ÉÅ„Çß„ÉÉ„ÇØ, „ÇØ„É™„ÉÉ„ÇØ„Åï„Çå„ÅüÊú¨‰∫∫„ÇÇÂ§ñ„Åô
          let rowOK = i >= 0 && i < this.currentSize.height;
          let columnOK = j >= 0 && j < this.currentSize.width;
          let isNotTheSquare = !(i == row && j == column);
          if (rowOK && columnOK && isNotTheSquare) {
            neighborsArray.push(this.boardArray[i][j]);
          }
        }
      }
      return neighborsArray;
    },
    setSize() {
      switch (this.pickedSize) {
        case "beginner":
          this.currentSize = { height: 9, width: 9, mines: 10 };
          break;
        case "intermediate":
          this.currentSize = { height: 9, width: 16, mines: 40 };
          break;
        case "advanced":
          this.currentSize = { height: 16, width: 30, mines: 99 };
          break;
        default:
          break;
      }
    },
    //Add mines Âú∞Èõ∑„ÇíÈÖçÁΩÆ
    addMines() {
      let mines = this.currentSize.mines;
      while (mines > 0) {
        let randomRow = Math.floor(Math.random() * this.currentSize.height);
        let randomColumn = Math.floor(Math.random() * this.currentSize.width);
        if (!this.boardArray[randomRow][randomColumn].mine) {
          this.boardArray[randomRow][randomColumn].mine = true;
          mines--;
        }
      }
    },
    //After mines placed, set numbers Âú∞Èõ∑„ÇíÈÖçÁΩÆ„Åó„Åü„ÅÇ„Å®„Ç≥„Éû„Å´Êï∞Â≠ó„ÇíËøΩÂä†
    addNumbers() {
      this.boardArray.forEach((row) =>
        row.forEach((square) => {
          if (square.mine) this.squaresWithMines.push(square);
        })
      );
      this.squaresWithMines.forEach((square) => {
        const neighbors = this.findNeighbors(square.row, square.column);
        neighbors.forEach((neighbor) => (neighbor.number += 1));
      });
    },
    setUpBoard() {
      this.setSize();
      let { height, width } = this.currentSize;
      //Put square objects in 2d array  „Éû„Çπ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÇíÔºíÊ¨°ÂÖÉÈÖçÂàó„Å´ÂÖ•„Çå„Çã
      let bigArray = new Array(height);
      for (let i = 0; i < height; i++) {
        let newRow = [];
        for (let j = 0; j < width; j++) {
          let squareObj = {
            row: i,
            column: j,
            number: 0,
            revealed: false,
            marked: false,
          };
          newRow.push(squareObj);
        }
        bigArray[i] = newRow;
      }
      this.boardArray = bigArray;
      this.addMines();
      this.addNumbers();
    },
  },
  created() {
    document.title = "Minesweeper";
    this.setUpBoard();
  },
};
</script>

